<template>
  <va-card :title="$t('tables.labelStock')" class="w-100">
    <va-button
      icon="fa fa-plus"
      flat
      slot="actions"
      class="mr-0"
      @click="showCreateModal"
    />
    <div class="row mr-0">
      <div class="flex xs12 sm4">
        <label class="input-label"> {{$t('forms.search')}} </label>
        <va-input v-model="searchText" :placeholder="$t('forms.search')" />
      </div>
      <va-button
        icon="fa fa-search"
        flat
        slot="actions"
        color="success"
        class="mr-0"
        @click="search"
      />
      <va-button
        icon="fa fa-trash"
        flat
        slot="actions"
        color="danger"
        class="mr-0"
        @click="deletesearch"
      />

    </div>
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      :no-pagination = "searchBool"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        <va-icon name="fa fa-circle" :color="props.rowData.color" size="8px" />
      </template>

      <template slot="actions" slot-scope="props">
        <va-button flat small color="gray" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.edit') }}
        </va-button>

        <va-button flat small color="danger" @click="showDeleteModal(props.rowData)" class="ma-0">
          {{ $t('tables.delete') }}
        </va-button>
      </template>
    </va-data-table>

    <va-modal v-model="deleteModal" no-outside-dismiss
      :title=" $t('generic.deleteItemTitle') "
      :okText=" $t('modal.delete') "
      :cancelText=" $t('modal.cancel') "
      :message=" $t('generic.deleteItemMsg') "
      @ok="okDeletefun()"
      @cancel="cancleDeletefun()">
    </va-modal>

    <va-modal v-model="editModal" no-outside-dismiss
      :title=" $t('generic.editItemTitle') "
      :okText=" $t('modal.edit') "
      :cancelText=" $t('modal.cancel') "
      @ok="okEditfun()"
      @cancel="cancleEditfun()">
      <div class="row">
        <div class="flex md12 sm12 xs12">
          <label class="input-label"> {{$t('placeholder.name')}} </label>
          <va-input v-model="currentobject.name" :placeholder="$t('placeholder.name')"/>
        </div>
      </div>
    </va-modal>

    <va-modal v-model="createModal" no-outside-dismiss
      :title=" $t('generic.creatItemTitle') "
      :okText=" $t('modal.create') "
      :cancelText=" $t('modal.cancel') "
      @ok="okCreatefun()"
      @cancel="cancleCreatefun()">
      <div class="row">
        <div class="flex md12 sm12 xs12">
          <label class="input-label"> {{$t('placeholder.name')}} </label>
          <va-input v-model="currentobject.name" :placeholder="$t('placeholder.name')"/>
        </div>
      </div>
    </va-modal>
  </va-card>

</template>

<script>
import StockService from '../../../../services/BasicData/StockService'

export default {
  data () {
    return {
      perPage: 10,
      totalPages: 0,
      dataTable: [],
      deleteModal: false,
      editModal: false,
      createModal: false,
      loading: true,
      opjectBeforeEdit: {},
      currentobject: {},
      searchText: '',
      searchBool: false,
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
      }, {
        name: 'name',
        title: this.$t('tables.headings.name'),
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
  },
  created () {
    this.readItems()
  },
  methods: {
    readItems (page = 1) {
      this.loading = true

      const params = {
        PageSize: this.perPage,
        PageNumber: page,
      }
      StockService.getAllPagnation(params)
        .then(response => {
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    search () {
      this.loading = true

      const params = {
        Text: this.searchText,
      }
      this.dataTable = []
      StockService.search(params)
        .then(response => {
          this.dataTable = response.data
          this.totalPages = Math.ceil(this.dataTable.length / 10)
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },

    deletesearch () {
      this.searchText = ''
      this.searchBool = false
      this.readItems()
    },
    create () {
      StockService.create(this.currentobject).then(response => {
        this.dataTable.push(response.data)
        if (response.headers['x-status'] === 'true') {
          this.loading = false
          this.currentobject = {}

          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    edit (user) {
      StockService.update(this.currentobject.id, this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          const idx = this.dataTable.findIndex(u => u.id === this.currentobject.id)
          this.dataTable[idx].name = this.currentobject.name
          this.currentobject = {}
          this.loading = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    remove (id) {
      StockService.delete(id).then(response => {
        if (response.headers['x-status'] === 'true') {
          const idx = this.dataTable.findIndex(u => u.id === id)
          this.dataTable.splice(idx, 1)
          this.loading = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },

    /* Delete model */
    showDeleteModal (cuobject) {
      this.deleteModal = true
      this.currentobject = cuobject
      this.loading = true
    },
    okDeletefun () {
      this.remove(this.currentobject.id)
    },
    cancleDeletefun () {
      this.loading = false
    },
    /* end Delete model */

    /* edit model */
    showEditModal (cuobject) {
      this.editModal = true
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.currentobject = cuobject
      this.loading = true
    },
    okEditfun () {
      this.edit(this.currentobject)
    },
    cancleEditfun () {
      this.loading = false
      console.log('ddd')
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.currentobject = {}
    },
    /* end edit model */

    /* create model */
    showCreateModal () {
      this.createModal = true
      this.loading = true
    },
    okCreatefun () {
      this.create()
    },
    cancleCreatefun () {
      this.loading = false
      this.currentobject = {}
    },
    /* end create model */

  },
}

</script>

<style lang="scss">
  i.fa.fa-exclamation {
    color: red !important;
  }

</style>
